import React, { Component } from "react";

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {};
		this.calculateAge = this.calculateAge.bind(this);
	}

	componentDidMount() {
		fetch("/visitorsCount")
			.then(res => res.text())
			.then(visitorsCount => {
				this.setState({ visitorsCount });
			});
	}

	componentDidUpdate() {
		fetch("/visitorsCount")
			.then(res => res.text())
			.then(visitorsCount => {
				this.setState({ age: this.state.age, visitorsCount });
			});
	}

	getBirthDay(dateOfBirth) {
		const birthDay = {
			date: +dateOfBirth.split("/")[0],
			month: +dateOfBirth.split("/")[1],
			year: +dateOfBirth.split("/")[2]
		};
		return birthDay;
	}

	calculateAge() {
		const dateOfBirth = document.getElementById("date-of-birth").value;
		const birthDay = this.getBirthDay(dateOfBirth);

		fetch("/age", {
			method: "POST",
			headers: { "content-type": "application/json" },
			body: JSON.stringify(birthDay)
		})
			.then(res => res.json())
			.then(age => {
				this.setState({ age });
			});
	}

	render() {
		if (!this.state.age) {
			return (
				<div className="App">
					<div>Total Visitors count : {this.state.visitorsCount}</div>
					<main>
						<section>
							<label> Date of birth : </label>
							<input type="text" id="date-of-birth" />
						</section>
						<button onClick={this.calculateAge}>Submit</button>
					</main>
				</div>
			);
		}
		return (
			<div className="App">
				<div>Total Visitors count : {this.state.visitorsCount}</div>
				<main>
					<p>
						You are {" - "}
						{this.state.age.year} years {this.state.age.month} month(s){" "}
						{this.state.age.day} day(s) old
					</p>
				</main>
			</div>
		);
	}
}

export default App;



